import { Link, useI18next } from "gatsby-plugin-react-i18next";

import { DropdownButton, Dropdown } from "react-bootstrap";

const LanguageSwitcher = () => {
  const { t, originalPath } = useI18next();

  return (
    <DropdownButton title={t("languageSwitcher.title")}>
      <Dropdown.Item as={Link} to={originalPath} language="ro">
        Română
      </Dropdown.Item>
      <Dropdown.Item as={Link} to={originalPath} language="en">
        English
      </Dropdown.Item>
    </DropdownButton>
  );
};

export default LanguageSwitcher;
